@import './../../_base.scss';


.home {
    width: 100%;
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    height: 92vh;

    background-color: $black-secondary;
    overflow-y: scroll;

}


@media(max-width: $breakpoint-mobile) {
    .home {
        justify-content: center;
        padding: 40px 0 0;
    }
}